<template>
    <div class="container back-one">
        <div class="card-list-add">
            <ul>
                <li>
                    <div class="fl">
                        <img src="../../static/img/9556b8_52x52.png" alt="">
                        <span>选择银行：</span>
                    </div>
                    <div class="fr">
                        <div class="select" @click="show=true">{{config.bankName || '请选择银行'}}</div>
                    </div>
                </li>
                <li>
                    <div class="fl">
                        <img src="../../static/img/ed4fef_52x52.png" alt="">
                        <span>支行名称：</span>
                    </div>
                    <div class="fr">
                        <input maxlength="20" v-model="config.subBankName" type="text" placeholder="请输入支行名称">
                    </div>
                </li>
                <li>
                    <div class="fl">
                        <img src="../../static/img/2d09d6_52x52.png" alt="">
                        <span>卡号：</span>
                    </div>
                    <div class="fr">
                        <b-input v-model="config.bankAccount" :maxLength="25" placeholder="请输入卡号"></b-input>
                    </div>
                </li>
                <li>
                    <div class="fl">
                        <img src="../../static/img/2d09d6_52x52.png" alt="">
                        <span>确认卡号：</span>
                    </div>
                    <div class="fr">
                        <b-input v-model="bankAccount" :maxLength="25" placeholder="请输入确认卡号"></b-input>
                    </div>
                </li>
                <li>
                    <div class="fl">
                        <img src="../../static/img/a11b8a_52x52.png" alt="">
                        <span>开户地：</span>
                    </div>
                    <div class="fr">
                        <input maxlength="20" v-model="config.location" type="text" placeholder="请输入开户地">
                    </div>
                </li>
                <li>
                    <div class="fl">
                        <img src="../../static/img/ed4fef_52x52.png" alt="">
                        <span>开户人：</span>
                    </div>
                    <div class="fr">
                        <input v-model="config.accountHolder" type="text" placeholder="请输入开户人姓名">
                    </div>
                </li>
            </ul>
            <div class="public-btn mt25">
                <button @click="addBank" class="button">确认添加</button>
            </div>
        </div>

        <van-popup
        v-model="show"
        position="bottom"
        :style="{ height: '40%' }"
        >
        <van-picker
            title="请选择银行"
            show-toolbar
            :columns="columns"
            @confirm="onConfirm"
            @cancel="show=false"
            visible-item-count="4"
        />
        </van-popup>
    </div>
</template>

<script>
import { GAME_GET_USER_BASE_INFO } from "@/tool/common/constants.js";
export default {
    data(){
        return {
            columns:['北京银行','工商银行', '光大银行', '广发银行', '华夏银行', '建设银行', '民生银行', '交通银行', '宁波银行','农业银行', '平安银行', '浦发银行', '上海银行', '深圳发展银行', '兴业银行', '邮政银行', '招商银行', '中国银行', '中信银行', '其他银行'],
            show: false,
            bankAccount:'',
            config: {
                bankName:'',
                subBankName: '',
                bankAccount: '',
                location: '',
                accountHolder: '',
            }
        }
    },
    created(){
        this.getInfo()
    },
    methods: {
        getInfo(){
            this.$store.dispatch(`Game/${GAME_GET_USER_BASE_INFO}`, this.baseUser)
            .then(res=>{
                this.config.accountHolder = res.name
            })
        },
        addBank(){
            if(this.bankAccount != ''){
                if(this.bankAccount != this.config.bankAccount){
                    this.$toast('两次卡号不一致')
                    return
                }
            }else{
                this.$toast('请输入卡号')
                return
            }
            var data = {
                ...this.baseUser,
                ...this.config
            }
            this.$api.user.addUserBank(data)
            .then(res=>{
                console.log(res);
                if(res.result == 1){
                    this.$toast('添加成功~')
                    setTimeout(() => {
                        this.$router.back(-1)
                    }, 2000);
                }else{
                    this.$toast(res.description)
                }
            })
        },
        onConfirm(value, index) {
            this.config.bankName = value
            this.show = false
        },
    },
}
</script>

